<template>
<div class="familyBox">
  <el-card class="box-card" style="border-radius: 10px;height: 85vh;position: relative">
    <el-table
      height="100vh"
      :data="infoList"
      stripe
      style="width: 100%">
      <el-table-column
        prop="application_id"
        label="序号"
        width="180">
      </el-table-column>
      <el-table-column
        prop="applicant_name"
        label="申请人姓名"
        width="180">
      </el-table-column>
      <el-table-column
        prop="applicant_person_id"
        label="申请人身份证号"
        width="180">
      </el-table-column>
      <el-table-column
        prop="relative_name"
        label="家属姓名">
      </el-table-column>
      <el-table-column
        align="right"
        label="操作">
        <template slot-scope="scope">
          <el-button
            type="text"
            @click.native.prevent="handleRow(scope.row)"
            size="small">
            处理
          </el-button>
        </template>
      </el-table-column>
      <el-table-column
        fixed="right"
        width="50">
        <template slot="header" slot-scope="scope">
          <el-button icon="el-icon-refresh" circle @click="handleRefresh"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="pageBox" style="bottom:0;position: absolute;width: 100%">
      <el-pagination
        layout="prev, pager, next"
        :current-page.sync="currentPage"
        :page-size="pageSize"
        @current-change="handleChange"
        :total="total">
      </el-pagination>
    </div>
    </el-card>
  <!--对话框-->
  <el-dialog
    title="处理"
    :visible.sync="dialogVisible"
    width="50%">
    <el-descriptions title="用户信息" :column="3">
      <el-descriptions-item label="序号":span="3">{{ form.application_id }}</el-descriptions-item>
      <el-descriptions-item label="申请人身份证号" :span="2">{{form.applicant_person_id}}</el-descriptions-item>
      <el-descriptions-item label="申请人姓名">{{form.applicant_name}}</el-descriptions-item>
      <el-descriptions-item label="家属身份证号" :span="2">{{form.relative_person_id }}</el-descriptions-item>
      <el-descriptions-item label="家属姓名">{{form.relative_name}}</el-descriptions-item>
      <el-descriptions-item label="申请关系">{{ showRelation()}}</el-descriptions-item>
      <el-descriptions-item label="附加信息">
        {{ form.additional_information===null?'无':form.additional_information }}
      </el-descriptions-item>
      <el-descriptions-item label="状态">
        <el-tag size="small">{{showState()}}</el-tag>
      </el-descriptions-item>
    </el-descriptions>
    <el-form :model="form" :rules="rules" ref="form" :hide-required-asterisk="true">
      <el-form-item label="审批意见" prop="is_succeed">
        <el-select v-model="form.is_succeed" placeholder="请选择审批意见">
          <el-option label="批准" value="1"></el-option>
          <el-option label="拒绝" value="0"></el-option>
        </el-select>
      </el-form-item>
    </el-form>

    <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="handleSubmit">提 交</el-button>
  </span>
  </el-dialog>
</div>
</template>

<script>

import cookie from "js-cookie";
import { getFamily } from "@/api/getFamily";
import { postFamily } from "@/api/postFamily";


export default {
  name: "FamilyInfo",
  data(){
    return{
      pageSize:8,//页面大小
      currentPage:1,// 当前显示页码
      total:0,//总条数,
      rules:{
        is_succeed:[
          {
            required: true,
            message: '请选择审批意见',
            trigger: 'change',
          }
        ]
      },
      form:{
      },
      dialogVisible:false,
      infoList:[]
    }
  },
  methods:{
    handleRefresh(){
      this.currentPage=1
      this.getInfo(this.currentPage)
    },
    getInfo(curPage){
      let param={token:cookie.get('token'), page:curPage, size:this.pageSize}
      getFamily(param).then(res=>{
        console.log(res)
        this.infoList=res.data.data.application_list
        this.total=res.data.data.total
        this.$message({
          message: '加载成功',
          type: 'success'
        });
      }).catch(err=>{
        console.log(err)
        this.$message.error('网络异常!')
      })
    },
    handleChange(val){
      console.log(val)
      this.getInfo(this.currentPage)
    },
    handleRow(row){
      if (this.$refs['form']!==undefined) {//防止验证信息残留
        this.$refs['form'].resetFields();
      }
      this.form=JSON.parse(JSON.stringify(row))
      this.form.is_succeed=null
      this.dialogVisible=true
    },
    showState(){
      if(this.form.is_processed===1){
        return "已查看"
      }else
        return "未完成"
    },
    handleSubmit(){
      this.$refs.form.validate((valid)=>{
        if(valid){
          this.dialogVisible = false;
          this.form.is_processed=1;
          this.form["token"]=cookie.get('token')
          console.log(this.form)
          postFamily(this.form).then(res=>{
            console.log(res)
          })
          this.currentPage=1
          this.getInfo(this.currentPage)
        }
      })
    },
    showRelation(){
      if(this.form.relation_type===0){
        return "伴侣"
      }else if(this.form.relation_type===1){
        return "血亲"
      }else if(this.form.relation_type===2){
        return "姻亲"
      }
    },
  },
  created() {
    //页面加载就获取数据
    //    let info=await getTester(cookie.get('token'))
    //    this.infoList=info.data.application_list
    //    this.total=this.infoList.length
    this.getInfo(this.currentPage)
    console.log('info',this.infoList)
    console.log('total',this.total)
  }
};
</script>

<style lang="less" scoped>
.familyBox{
  height: 98%;
  .pageBox{
    text-align: center;
  }
}
</style>