<template>
<div class="reportBox">
  <el-card class="box-card" style="border-radius: 10px;height: 85vh;position: relative">

    <el-table
      :data="infoList"
      stripe
      height="100vh"
      style="width: 100%">

      <el-table-column
        prop="report_id"
        label="序号"
        width="180">
      </el-table-column>
      <el-table-column
        prop="person_name"
        label="姓名"
        width="180">
      </el-table-column>
      <el-table-column
        prop="person_id"
        label="身份证号"
        width="180">
      </el-table-column>
      <el-table-column
        prop="from"
        label="来自"
        width="180">
      </el-table-column>
      <el-table-column
        prop="to"
        label="去往"
        width="180">
      </el-table-column>
      <el-table-column
        align="right"
        label="操作"
      >
        <template slot-scope="scope">
          <el-button
            @click.native.prevent="handleRow(scope.row)"
            type="text"
            size="small">
            处理
          </el-button>
        </template>
      </el-table-column>
      <el-table-column
        fixed="right"
        width="50">
        <template slot="header" slot-scope="scope">
          <el-button icon="el-icon-refresh" circle @click="handleRefresh"></el-button>
        </template>
      </el-table-column>

    </el-table>
    <div class="pageBox" style="bottom:0;position: absolute;width: 100%">
      <el-pagination
        layout="prev, pager, next"
        :current-page.sync="currentPage"
        :page-size="pageSize"
        @current-change="handleChange"
        :total="total">
      </el-pagination>
    </div>
  </el-card>
  <el-dialog
    title="处理"
    :visible.sync="dialogVisible"
    width="50%">

    <el-descriptions title="报备信息">
      <el-descriptions-item label="序号">{{ form.report_id }}</el-descriptions-item>
      <el-descriptions-item label="姓名">{{form.person_name}}</el-descriptions-item>
      <el-descriptions-item label="身份证号">{{ form.person_id }}</el-descriptions-item>
      <el-descriptions-item label="来自">{{ form.from }}</el-descriptions-item>
      <el-descriptions-item label="去往">{{ form.to }}</el-descriptions-item>
      <el-descriptions-item label="状态">
        <el-tag size="small">{{showState()}}</el-tag>
      </el-descriptions-item>
      <el-descriptions-item label="附加信息">
        {{ form.additional_info===null?'无':form.additional_info }}
<!--        <img :src="form.img_url" alt="lost">-->
      </el-descriptions-item>
    </el-descriptions>
    <el-form :model="form" :rules="rules" ref="form" :hide-required-asterisk="true">
      <el-form-item label="审批意见" prop="is_allowed">
        <el-select v-model="form.is_allowed" placeholder="请选择审批意见">
          <el-option label="批准" value="1"></el-option>
          <el-option label="拒绝" value="0"></el-option>
        </el-select>
      </el-form-item>
    </el-form>

    <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="handleSubmit">提 交</el-button>
  </span>
  </el-dialog>
</div>
</template>

<script>
import { getRemote } from "@/api/getRemote";
import { postRemote } from "@/api/postRemote";
import cookie from "js-cookie";
export default {
  name: "ReportingList",
  data(){
    return{
      pageSize:8,//页面大小
      currentPage:1,// 当前显示页码
      total:0,//总条数,
      rules:{
        is_allowed:[
          {
            required: true,
            message: '请选择审批意见',
            trigger: 'change',
          }
        ]
      },
      form:{
      },
      infoList:[],
      dialogVisible:false,
    }
  },
  methods:{
    handleRow(row){
      // this.$nextTick(()=>{
      //   this.$refs['form'].resetFields();
      // })
      if (this.$refs['form']!==undefined) {//防止验证信息残留
        this.$refs['form'].resetFields();
      }
      this.form=JSON.parse(JSON.stringify(row))
      this.form.is_allowed=null
      this.dialogVisible=true
    },
    handleSubmit(){
      this.$refs.form.validate((valid)=>{
        if(valid){
          this.dialogVisible = false;
          this.form.is_checked=1;
          this.form["token"]=cookie.get('token')
          console.log(this.form)
          postRemote(this.form).then(res=>{
            console.log(res)
          })
          this.currentPage=1
          this.getInfo(this.currentPage)
        }
      })
    },
    showState(){
      if(this.form.is_checked===1){
        return "已查看"
      }else
        return "未完成"
    },
    handleChange(val){
      console.log(val)
      this.getInfo(this.currentPage)
    },
    handleRefresh(){
      this.currentPage=1
      this.getInfo(this.currentPage)
    },
    getInfo(curPage){
      let param={token:cookie.get('token'), page:curPage, size:this.pageSize}
      getRemote(param).then(res=>{
        console.log(res)
        this.infoList=res.data.data.reporting_list
        this.total=res.data.data.total
        this.$message({
          message: '加载成功',
          type: 'success'
        });
      }).catch(err=>{
        console.log(err)
        this.$message.error('网络异常!')
      })
    },
  },
  created() {
    //页面加载就获取数据
    //    let info=await getTester(cookie.get('token'))
    //    this.infoList=info.data.application_list
    //    this.total=this.infoList.length
    this.getInfo(this.currentPage)
    console.log('info',this.infoList)
    console.log('total',this.total)
  }
};
</script>

<style lang="less" scoped>
.reportBox{
  height:98%;
  .pageBox{
    text-align: center;
  }
}

</style>