<template>
  <div class="MainHeader">
    <div class="HeaderLeft">
      <el-breadcrumb separator-class="el-icon-arrow-right">
<!--        <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>-->
<!--        <el-breadcrumb-item><a href="/">活动管理</a></el-breadcrumb-item>-->
<!--        <el-breadcrumb-item>活动列表</el-breadcrumb-item>-->
        <el-breadcrumb-item v-for="item in breadCrumbList" :key="item.path" :to="item.path">
          {{ item.meta.title }}
        </el-breadcrumb-item>
      </el-breadcrumb>
    </div>

    <div class="HeaderRight">
      <h3 class="time"><i class="el-icon-time"> {{time}}</i></h3>
      <el-dropdown @command="handleCommand" size="mini">
      <span class="el-dropdown-link">
      <el-avatar icon="el-icon-user-solid"></el-avatar>
      </span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item command="center">个人中心</el-dropdown-item>
          <el-dropdown-item command="logout">退出登录</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>

    </div>
  </div>
</template>

<script>
import cookie from "js-cookie";
export default {
  name: "HeaderMenu",
  data(){
    return{
      time:""
    }
  },
  methods: {
    handleCommand(command) {
      if (command === "logout") {
        cookie.remove("token");
        this.$router.push("/");
      } else if (command === "center") {
        this.$router.push("/home/center");
      }
    },
    currentTime() {
      setInterval(this.getTime, 500);
    },
    getTime() {
      let yy = new Date().getFullYear();
      let mm = new Date().getMonth() + 1;
      let dd = new Date().getDate();
      let hh = new Date().getHours();
      let mf =
        new Date().getMinutes() < 10
          ? "0" + new Date().getMinutes()
          : new Date().getMinutes();
      let ss =
        new Date().getSeconds() < 10
          ? "0" + new Date().getSeconds()
          : new Date().getSeconds();
      this.time =
        yy + "年" + mm + "月" + dd + "日 " + hh + ":" + mf + ":" + ss;
    }
  },
  mounted() {
    this.currentTime();
  },

  computed:{
    breadCrumbList(){
      return this.$route.matched;
    }
  }

};
</script>

<style lang="less" scoped>
.alHeader {
  line-height: 60px;
}

.MainHeader {
  .HeaderLeft{
    /deep/.el-breadcrumb{
      .el-breadcrumb__item{
        .el-breadcrumb__inner{
          color: #ababab;
        }
        &:last-child{
          .el-breadcrumb__inner{
            color: white;
          }
        }
      }

    }
  }
  background-color: #65696b;
  height: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
  .HeaderRight{
    display: flex;
    align-items: center;
    .time{
      margin-right: 10px;
      color: white;
      font-size: 16px;

    }
    .el-dropdown {
      padding: 3px 0 0 0;
    }
  }



}
</style>