<template>
<div class="complaintBox">

  <el-card class="box-card" style="border-radius: 10px;height: 85vh;position: relative">
    <!--    刷新按钮-->
    <!--    列表-->
  <el-table
    :data="infoList"
    stripe
    height="100vh"
    style="width: 100%">
    <el-table-column
      prop="application_id"
      label="序号"
      width="180">
    </el-table-column>
    <el-table-column
      prop="person_name"
      label="姓名"
      width="180">
    </el-table-column>
    <el-table-column
      prop="person_id"
      label="身份证号"
      width="180">
    </el-table-column>
    <el-table-column
      prop="phone_number"
      label="联系电话"
      width="180">
    </el-table-column>
    <el-table-column
      label="申诉类型">
      <template slot-scope="scope">
        {{showType(scope.row.type)}}
      </template>
    </el-table-column>
    <el-table-column
      align="right"
      label="操作">
      <template slot-scope="scope">
        <el-button
          @click.native.prevent="handleRow(scope.row)"
          type="text"
          size="small">
          处理
        </el-button>
      </template>
    </el-table-column>
    <el-table-column
      fixed="right"
      width="50">
    <template slot="header" slot-scope="scope">
      <el-button icon="el-icon-refresh" circle @click="handleRefresh"></el-button>
    </template>
    </el-table-column>

  </el-table>
    <!--    页码-->
    <div class="pageBox" style="bottom:0;position: absolute;width: 100%">
      <el-pagination
        layout="prev, pager, next"
        :current-page.sync="currentPage"
        :page-size="pageSize"
        @current-change="handleChange"
        :total="total">
      </el-pagination>
    </div>
  </el-card>

  <!--对话框-->
  <el-dialog
    title="处理"
    :visible.sync="dialogVisible"
    width="50%">

    <el-descriptions title="用户信息">
      <el-descriptions-item label="序号">{{ form.application_id }}</el-descriptions-item>
      <el-descriptions-item label="姓名">{{form.person_name}}</el-descriptions-item>
      <el-descriptions-item label="手机号">{{ form.phone_number }}</el-descriptions-item>
      <el-descriptions-item label="身份证号">{{ form.person_id }}</el-descriptions-item>
      <el-descriptions-item label="类型">{{showType(form.type)}}</el-descriptions-item>
      <el-descriptions-item label="附加信息">
        {{ (form.additional_info===null||form.additional_info==='')?'无':form.additional_info }}
      </el-descriptions-item>
    </el-descriptions>
    <el-form :model="form" :rules="rules" ref="form" :hide-required-asterisk="true">
      <el-form-item label="审批意见" prop="is_processed">
        <el-select v-model="form.is_processed" placeholder="是否处理">
          <el-option label="已处理" value="1"></el-option>
          <el-option label="未处理" value="0"></el-option>
        </el-select>
      </el-form-item>
    </el-form>

    <span slot="footer" class="dialog-footer">
    <el-button @click="dialogVisible = false">取 消</el-button>
    <el-button type="primary" @click="handleSubmit">提 交</el-button>
  </span>
  </el-dialog>
</div>
</template>

<script>
import cookie from "js-cookie";
import { getComplaint } from "@/api/getComplaint";
import { postComplaint } from "@/api/postComplaint";
export default {
  name: "ComplaintList",
  data(){
    return{
      pageSize:8,//页面大小
      currentPage:1,// 当前显示页码
      total:0,//总条数,
      rules:{
        is_processed:[
          {
            required: true,
            message: '请选择审批意见',
            trigger: 'change',
          }
        ]
      },
      form:{
        additional_info: "",
        person_name: "fy",
        phone_number: "13317278716",
        application_id:"15896953",
        is_processed:0,
        is_investigated:0,
        type:"健康码异常"
    },
      infoList:[],
      dialogVisible:false,
      testList:[
        {
          additional_info: "",
          person_name: "fy",
          phone_number: "13317278716",
          application_id:"15896953",
          is_processed:0,
          is_investigated:0,
          type:"健康码异常"
        },
        {
          additional_info: "",
          person_name: "fy",
          phone_number: "13317278716",
          application_id:"15896953",
          is_processed:0,
          is_investigated:0,
          type:"健康码异常"
        },
      ]
    }
  },
  methods:{
    showType(type){
      // 0 健康码
      // 1 行程码
      // 2 核酸检测
      // 3 疫苗接种
      if(type===0){
        return "健康码"
      }else if(type===1){
        return "行程码"
      }else if(type===2){
        return "核酸检测"
      }else if(type===3){
        return "疫苗接种"
      }
    },
    handleRefresh(){
      this.currentPage=1
      this.getInfo(this.currentPage)
    },
    getInfo(curPage){
      let param={token:cookie.get('token'), page:curPage, size:this.pageSize}
      getComplaint(param).then(res=>{
        console.log(res)
        this.infoList=res.data.data.abnormal_list
        this.total=res.data.data.total
        this.$message({
          message: '加载成功',
          type: 'success'
        });
      }).catch(err=>{
        console.log(err)
        this.$message.error('网络异常!')
        const h = this.$createElement;
        this.$notify({
          title: '网络异常',
          message: h('i', { style: 'color: teal'}, '请尝试重新登录')
        });
      })
    },

    handleChange(val){
      console.log(val)
      this.getInfo(this.currentPage)
    },
    handleRow(row){
      if (this.$refs['form']!==undefined) {//防止验证信息残留
        this.$refs['form'].resetFields();
      }
      this.form=JSON.parse(JSON.stringify(row))
      this.form.is_processed=null
      this.dialogVisible=true
    },
    showState(){
      if(this.form.is_processed===1){
        return "已查看"
      }else
        return "未完成"
    },
    handleSubmit(){
      this.$refs.form.validate((valid)=>{
        if(valid){
          this.dialogVisible = false;
          this.form["token"]=cookie.get('token')
          console.log(this.form)
          postComplaint(this.form).then(res=>{
            console.log(res)
          })
          this.currentPage=1
          this.getInfo(this.currentPage)
        }
      })
    }
  },
  created() {
    //页面加载就获取数据
    //    let info=await getTester(cookie.get('token'))
    //    this.infoList=info.data.application_list
    //    this.total=this.infoList.length
    this.getInfo(this.currentPage)
    console.log('info',this.infoList)
    console.log('total',this.total)
  }


};
</script>

<style lang="less" scoped>
.complaintBox{
  height: 98%;
  .pageBox{
    text-align: center;
  }
}
</style>