<template>
<div class="colorBox">
  <el-form :inline="true" :model="form" :rules="rules" ref="form" :hide-required-asterisk="true">
    <el-form-item label="城市" prop="selectedOptions">
      <el-cascader
        placeholder="请选择城市"
        size="large"
        :options="cities"
        v-model="form.selectedOptions">
      </el-cascader>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="Search">查询</el-button>
    </el-form-item>
  </el-form>
  <el-card class="box-card" style="border-radius: 10px;height: 75vh">
    <el-empty v-if="!flag" description="请选择想查询的城市"></el-empty>
    <el-row v-if="flag">
      <el-col :span="10">
        <el-descriptions  direction="vertical" :column="4" border>
          <el-descriptions-item label="地区" :span="4">
            {{(getCodeToText(this.form.selectedOptions)||"").replace(/:/g,'')}}
          </el-descriptions-item>
          <el-descriptions-item label="总计" >
            <el-tag size="small">{{ colorData.green+colorData.yellow+colorData.red }}</el-tag>
          </el-descriptions-item>
          <el-descriptions-item label="绿码" >
            <el-tag size="small" type="success">{{ colorData.green }}</el-tag>
          </el-descriptions-item>
          <el-descriptions-item label="黄码">
            <el-tag size="small" type="warning">{{ colorData.yellow}}</el-tag>
          </el-descriptions-item>
          <el-descriptions-item label="红码" >
            <el-tag size="small" type="danger">{{ colorData.red }}</el-tag>
          </el-descriptions-item>
        </el-descriptions>
      </el-col>
      <el-col :span="14">
        <e-charts class="chart" :option="chartOption"/>
      </el-col>
    </el-row>
  </el-card>

</div>
</template>

<script>
import { getCodeNumber } from "@/api/getCodeNumber";
import {regionData,CodeToText,provinceAndCityData,} from 'element-china-area-data'
import cookie from "js-cookie";
export default {
  name: "CodeColor",
  data(){
    return{
      cityName:'null',
      flag:false,
      form:{
        selectedOptions:'',
      },
      colorData:'',
      rules:{
        selectedOptions:[
          {
            required: true,
            message: '请选择位置信息',
            trigger: 'change',
          }
        ],
      },
      cities:regionData,
    }
  },
  computed:{
    chartOption(){
      return{
        title: {
          text: '红黄码人数统计',
          subtext:this.cityName,
          left: 'center'
        },
        tooltip: {
          trigger: 'item'
        },
        legend: {
          orient: 'horizontal',
          left: 'center',
          top:'bottom',
        },
        series: [
          {
            name: '人数',
            type: 'pie',
            radius: '50%',
            data: [
              { value: this.colorData.green, name: '绿码',itemStyle: { color: '#66c03b' } },
              { value: this.colorData.yellow, name: '黄码',itemStyle: { color: '#f1af50' } },
              { value: this.colorData.red, name: '红码',itemStyle: { color: '#f17173' } }
            ],
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          }
        ]
      }
    }
  },
  methods:{
    getCodeToText(codeArray) {
      let area = "";
      switch (codeArray.length) {
        case 1:
          area += CodeToText[codeArray[0]];
          break;
        case 2:
          area += CodeToText[codeArray[0]] + ":" + CodeToText[codeArray[1]];
          break;
        case 3:
          area +=
            CodeToText[codeArray[0]] +
            ":" +
            CodeToText[codeArray[1]] +
            ":" +
            CodeToText[codeArray[2]];
          break;
        default:
          break;
      }
      return area;
    },
    Search(){
      this.$refs.form.validate(valid=>{
        if(valid){
          let tmp=this.getCodeToText(this.form.selectedOptions)
          this.form.province=tmp.split(':')[0]
          this.form.city=tmp.split(':')[1]
          this.form.district=tmp.split(':')[2]
          this.cityName=this.form.province+this.form.city+this.form.district
          let param={token:cookie.get('token'), province:this.form.province, city:this.form.city, district:this.form.district}
          getCodeNumber(param).then(res=>{
            console.log(res)
            this.colorData=res.data.data
            this.flag=true
          }).catch(err=>{
            console.log(err)
          })
        }
      })
    },
  },
};
</script>

<style lang="less" scoped>
.chart{
  height: 500px;
}
</style>