import VueRouter from 'vue-router';
import webLogin from '@/pages/WebLogin';
import homePage from "@/pages/HomePage";
import personCertification from "@/pages/audit/PersonCertification";
import userCenter from "@/pages/UserCenter";
import complaintList from "@/pages/audit/ComplaintList";
import testingPage from "@/pages/audit/TestingPage";
import tracePage from "@/pages/statistics/TracePage";
import reportingList from "@/pages/audit/ReportingList";
import familyInfo from "@/pages/audit/FamilyInfo";
import siteCode from "@/pages/assignment/SiteCode";
import collectionPoint from "@/pages/assignment/CollectionPoint";
import codeColor from "@/pages/statistics/CodeColor";
import testResult from "@/pages/statistics/TestResult";
import infoPage from "@/pages/search/InfoPage";
import riskRegion from "@/pages/search/RiskRegion";
export default new VueRouter({
	routes: [
		{
			path: '/',
			name: 'login',
			meta:{title:'登录'},
			component: webLogin,
		},
		{
			path: '/home',
			name: 'home',
			component: homePage,
			meta:{title:'首页'},
			children:[
				{//个人主页
					path:'center',
					name:'center',
					component:userCenter,
					meta:{title:'个人中心'},
				},
				{//人员认证
					path:'certification',
					name:'certification',
					component:personCertification,
					meta:{title:'人员认证'},
				},
				{//异地报备
					path:'reporting',
					name:'reporting',
					component:reportingList,
					meta:{title: '报备审查'}
				},
				{//风险地区
					path:'riskRegion',
					name:'riskRegion',
					component:riskRegion,
					meta:{title: '风险地区查询'}
				},
				{//行程
					path:'trace',
					name:'trace',
					component:tracePage,
					meta:{title: '行程查询'}
				},
				{ //异常信息
					path:'complaint',
					name:'complaint',
					component:complaintList,
					meta:{title:'异常信息审查'},
				},
				{//家属
					path:'family',
					name:'family',
					component:familyInfo,
					meta:{title:'家属信息'},
				},
				{//场所码
					path:'siteCode',
					name:'siteCode',
					component:siteCode,
					meta:{title:'发放场所码'},
				},
				{//采集点
					path:'collection',
					name:'collection',
					component:collectionPoint,
					meta:{title:'设立采集点'},
				},
				{//用户信息
					path:'info',
					name:'info',
					component:infoPage,
					meta:{title:'用户信息'},
				},
				{//核酸检测结果统计
					path:'testResult',
					name:'testResult',
					component:testResult,
					meta:{title:'核酸检测结果统计'},
				},
				{//红黄码统计
					path:'codeResult',
					name:'codeResult',
					component:codeColor,
					meta:{title:'红黄码统计'},
				},
				{
					path:'test',
					name:'test',
					component:testingPage,
					meta:{title:'测试页面'},
				}


			]
		},
	],
});
