<template>
<div class="mainBox">
  <div>
    <el-form :inline="true" :model="form" :rules="rules" ref="form" class="demo-form-inline" :hide-required-asterisk="true">
      <el-form-item label="身份证号" prop="person_id">
        <el-input v-model.trim="form.person_id" placeholder="请输入所查询用户的身份证号" style="width: 220px"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="Search">查询</el-button>
        <el-button @click="resetForm">重置</el-button>
      </el-form-item>
    </el-form>
  </div>
  <div class="infoBox">
  <el-tabs type="border-card">
    <el-tab-pane label="基本信息">
      <p v-if="Object.values(usrInfo).length<=0" style="color: #ababab;font-size: medium">请输入身份证号进行查询</p>
      <el-descriptions class="margin-top" title="用户信息" :column="3"  border direction="vertical" v-if="Object.values(usrInfo).length>0">
        <el-descriptions-item>
          <template slot="label">
            <i class="el-icon-user"></i>
            姓名
          </template>
          {{usrInfo.person_name}}
        </el-descriptions-item>
        <el-descriptions-item>
          <template slot="label">
            <i class="el-icon-mobile-phone"></i>
            手机号
          </template>
          {{usrInfo.phone_number}}
        </el-descriptions-item>
        <el-descriptions-item>
          <template slot="label">
            <i class="el-icon-document"></i>
             健康码状态
          </template>
          <el-tag size="small" :type="tagColor()">{{colorName()}}</el-tag>
        </el-descriptions-item>
        <el-descriptions-item>
          <template slot="label">
            <i class="el-icon-tickets"></i>
            性别
          </template>
          <el-tag size="small">{{usrInfo.gender===0?'男':'女'}}</el-tag>
        </el-descriptions-item>
        <el-descriptions-item>
          <template slot="label">
            <i class="el-icon-office-building"></i>
            身份证号
          </template>
          {{usrInfo.person_id}}
        </el-descriptions-item>
      </el-descriptions>
    </el-tab-pane>
    <el-tab-pane label="核酸信息">
      <p v-if="Object.values(usrInfo).length<=0" style="color: #ababab;font-size: medium">请输入身份证号进行查询</p>
      <template>
        <el-table
          v-if="Object.values(usrInfo).length>0"
          :data="nucleicInfo"
          border
          style="width: 100%">
          <el-table-column
            prop="test_time"
            label="检测时间"
            width="180">
          </el-table-column>
          <el-table-column
            label="检测结果"
            width="180">
            <template slot-scope="scope">
              {{ scope.row.test_result === 0 ? "阴性" : "阳性" }}
            </template>
          </el-table-column>
          <el-table-column
            prop="institution_name"
            label="检测机构"
            width="180">
          </el-table-column>
          <el-table-column
            prop="transfer_code"
            label="转运码">
          </el-table-column>
        </el-table>
      </template>
    </el-tab-pane>
    <el-tab-pane label="接种信息">
      <p v-if="Object.values(usrInfo).length<=0" style="color: #ababab;font-size: medium">请输入身份证号进行查询</p>
      <template>
        <el-table
          v-if="Object.values(usrInfo).length>0"
          :data="vaccineInfo"
          border
          style="width: 100%">
          <el-table-column
            prop="inoculation_time"
            label="接种时间"
            width="180">
          </el-table-column>
          <el-table-column
            prop="inoculation_facility"
            label="接种机构"
            width="180">
          </el-table-column>
          <el-table-column
            prop="vaccine_name"
            label="疫苗名称"
            width="180">
          </el-table-column>
          <el-table-column
            prop="inoculation_number"
            label="接种剂次">
          </el-table-column>
        </el-table>
      </template>
    </el-tab-pane>
  </el-tabs>
</div>
</div>
</template>

<script>
import cookie from "js-cookie";
import { getUserInfo } from "@/api/getUserInfo";
import { getNucleic } from "@/api/getNucleic";
import { getVaccine } from "@/api/getVaccine";


export default {
  name: "InfoPage",
  data(){
    return{
      form:{person_id:''},
      nucleicInfo:[],
      vaccineInfo:[],
      usrInfo:{},
      rules:{
        person_id:[
          {
            required: true,
            message: '请输入身份证号',
            trigger: 'blur',
          }
        ]
      },
    }
  },
  methods:{
    colorName(){
      if(this.usrInfo.health_code_color===0){
        return "绿码"
      }else if(this.usrInfo.health_code_color===1){
        return "黄码"
      }else if(this.usrInfo.health_code_color===2){
        return "红码"
      }
    },
    tagColor(){
      if(this.usrInfo.health_code_color===0){
        return "success"
      }else if(this.usrInfo.health_code_color===1){
        return "warning"
      }else if(this.usrInfo.health_code_color===2){
        return "danger"
      }
    },
    resetForm(){
      this.$refs.form.resetFields();
    },
    Search(){
      this.$refs.form.validate((valid)=>{
        if(valid){
          this.form.token=cookie.get('token')
          getUserInfo(this.form).then(res=>{
            console.log('信息',res)
            if(res.data.message==="OK"){
              this.usrInfo=res.data.data
              this.$message({
                message: '加载成功',
                type: 'success'
              });
            }else{
              this.$message({
                message: '请检查所查询身份证号是否正确',
                type: 'warning'
              });
            }
          }).catch(err=>{
            console.log(err)
            this.$message.error('网络异常');
          })
          getNucleic(this.form).then(res=>{
            console.log('核酸',res)
            this.nucleicInfo=res.data.data.info_list
          })
          getVaccine(this.form).then(res=>{
            console.log('疫苗',res)
            this.vaccineInfo=res.data.data.info_list
          })

        }
      })
    }
  }
};
</script>

<style lang="less" scoped>
.mainBox{
  height:100%;
}
</style>