<template>
  <div class="mainBox">
    <div style="margin-bottom: 10px">
      <el-button type="primary" icon="el-icon-refresh"  @click="handleRefresh">刷新</el-button>
    </div>
    <div class="siteBox">
      <el-table
        :data="infoList"
        stripe
        height="95%"
        style="width: 100%">
        <el-table-column
          prop="application_id"
          label="序号"
          width="180">
        </el-table-column>
        <el-table-column
          prop="applicant_name"
          label="申请人"
          width="180">
        </el-table-column>
        <el-table-column
          prop="applicant_person_id"
          label="身份证号"
          width="180">
        </el-table-column>
        <el-table-column
          prop="place_name"
          label="场所名称"
          width="180">
        </el-table-column>
        <el-table-column
          label="场所位置">
          <template slot-scope="scope">
            {{(scope.row.position||"").replace(/:/g,'')}}
          </template>
        </el-table-column>
        <el-table-column
          label="操作"
          width="120"
          fixed="right">
          <template slot-scope="scope">
            <el-button
              @click.native.prevent="handleRow(scope.row)"
              type="text"
              size="small">
              处理
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="pageBox" style="bottom:0;position: absolute;width: 100%;text-align: center;">
        <el-pagination
          layout="prev, pager, next"
          :current-page.sync="currentPage"
          :page-size="pageSize"
          @current-change="handleChange"
          :total="total">
        </el-pagination>
      </div>
    </div>

    <el-dialog
      title="详情"
      :visible.sync="dialogVisible"
      width="50%">
      <el-descriptions title="场所信息">
        <el-descriptions-item label="序号">{{ form.application_id }}</el-descriptions-item>
        <el-descriptions-item label="申请人姓名">{{form.applicant_name}}</el-descriptions-item>
        <el-descriptions-item label="身份证号">{{ form.applicant_person_id }}</el-descriptions-item>
        <el-descriptions-item label="场所名称">{{ form.place_name }}</el-descriptions-item>
        <el-descriptions-item label="场所类型"><el-tag size="mini">{{ form.type }}</el-tag></el-descriptions-item>
        <el-descriptions-item label="详细位置">{{(form.position||"").replace(/:/g,'')+form.location}}</el-descriptions-item>
      </el-descriptions>
            <el-form label-position="left" label-width="80px" :model="form" ref="form" :rules="rules">
              <el-form-item label="审批意见" prop="is_succeed">
                <el-select v-model="form.is_passed"  placeholder="请选择审批意见">
                  <el-option label="批准" value=1></el-option>
                  <el-option label="拒绝" value=0></el-option>
                </el-select>
              </el-form-item>
            </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="handleCancel">取 消</el-button>
        <el-button type="primary" @click="handleSubmit">确 定</el-button>
      </span>
    </el-dialog>
  </div>

</template>

<script>
import cookie from "js-cookie";
import { getSiteCode } from "@/api/getSiteCode";
import { postTester } from "@/api/postTester";
import { postSiteCode } from "@/api/postSiteCode";
export default {
  name: "SiteCode",
  data(){
    return{
      pageSize:8,//页面大小
      currentPage:1,// 当前显示页码
      total:0,//总条数,
      infoList:[],
      dialogVisible:false,
      rules:{
        is_passed:[
          {
            required: true,
            message: '请选择审批意见',
            trigger: 'change',
          }
        ]
      },
      form:{
        application_id:'',
        applicant_name:'',
        collection_point_principal:'',
        collection_point_contact_phone:'',
        position:'' +
          '',
        is_passed:'',
      },
    }
  },
  methods:{
    handleSubmit(){
      this.$refs.form.validate((valid)=>{
        if(valid){
          this.dialogVisible = false;
          this.form["token"]=cookie.get('token')
          console.log(this.form)
          postSiteCode(this.form).then(res=>{
            console.log(res)
          })
          this.currentPage=1
          this.getInfo(this.currentPage)
        }
      })
    },
    handleCancel(){
      this.$refs.form.resetFields();
      this.dialogVisible = false;
    },
    handleRow(row){
      if (this.$refs['form']!==undefined) {//防止验证信息残留
        this.$refs['form'].resetFields();
      }
      this.form=JSON.parse(JSON.stringify(row))
      this.form.is_passed=null
      this.dialogVisible=true
    },
    handleChange(val){
      console.log(val)
      this.getInfo(this.currentPage)
    },
    handleAdd(){
      this.dialogVisible=!this.dialogVisible
    },
    getInfo(curPage){
      let param={token:cookie.get('token'), page:curPage, size:this.pageSize}
      getSiteCode(param).then(res=>{
        console.log(res)
        this.infoList=res.data.data.application_list
        this.total=res.data.data.total
        this.$message({
          message: '加载成功',
          type: 'success'
        });
      }).catch(err=>{
        console.log(err)
        this.$message.error('网络异常!')
      })
    },
    handleRefresh(){
      this.currentPage=1
      this.getInfo(this.currentPage)
    },
  },
  created() {
    //页面加载就获取数据
    this.getInfo(this.currentPage)
    console.log('info',this.infoList)
    console.log('total',this.total)
  }
};
</script>

<style lang="less" scoped>

.mainBox{
  height: 100%;
  .siteBox{
    padding: 7px;
    height: 90%;
    background-color: white;
    border-radius:5px;
    position:relative;
  }
}

</style>