<template>
<div class="traceBox">
  <el-form :inline="true" :model="form" :rules="rules" ref="form" class="demo-form-inline" :hide-required-asterisk="true">
    <el-form-item label="身份证号" prop="person_id">
      <el-input v-model="form.person_id" placeholder="请输入所查询用户的身份证号" style="width: 220px"></el-input>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" @click="Search">查询</el-button>
    </el-form-item>
  </el-form>

  <el-card class="box-card" style="border-radius: 10px;height: 75vh">
      <el-table
        :data="tableData"
        height="70vh"
        :default-sort = "{prop: 'date', order: 'descending'}"
        style="width: 100%">
        <el-table-column
          prop="person_name"
          label="姓名"
          width="180">
        </el-table-column>
        <el-table-column
          prop="record_time"
          label="时间"
          sortable
          width="280">
        </el-table-column>
        <el-table-column
          prop="place_name"
          label="途经">
        </el-table-column>
      </el-table>
  </el-card>

</div>
</template>

<script>
import { getTrace } from "@/api/getTrace";
import cookie from "js-cookie";
export default {
  name: "TracePage",
  data(){
    return{
      testList: [
        {
          person_name:"a",
          record_time:"2020-09-11 17:55:41",
          place_name:"慈利",
        },

      ],
      tableData:[],
      form:{
        token:"",
        person_id:"",
      },
      rules:{
        person_id:[
          {
            required: true,
            message: '请输入身份证号',
            trigger: 'blur',
          }
        ]
      }
    }
  },
  methods:{
    Search(){
      this.$refs.form.validate((valid)=>{
        if(valid){
          this.form.token=cookie.get('token')
          getTrace(this.form).then(res=>{
            console.log(res)
            this.tableData=res.data.data.itinerary_info

          })
        }
      })
    }
  }
};
</script>
<style lang="less" scoped>
.traceBox{
  height:90%;
}

</style>