<template>
  <div class="manageBox">

    <div class="box-card" >
      <!--    刷新按钮-->

      <!--    列表-->
      <el-table
        :data="infoList"
        max-height="600"
        stripe
        style="width: 100% ">
        <el-table-column
          prop="application_id"
          label="序号"
          width="180">
        </el-table-column>
        <el-table-column
          prop="applicant_name"
          label="姓名"
          width="180">
        </el-table-column>
        <el-table-column
          prop="applicant_phone"
          label="联系电话"
          width="180">
        </el-table-column>
        <el-table-column
          prop="applicant_person_id"
          label="身份证号">
        </el-table-column>
        <el-table-column
          align="right"
          label="操作">
          <template slot-scope="scope">
            <el-button
              @click.native.prevent="handleRow(scope.row)"
              type="text"
              size="small">
              处理
            </el-button>
          </template>
        </el-table-column>
        <el-table-column
          fixed="right"
          width="50">
          <template slot="header" slot-scope="scope">
            <el-button icon="el-icon-refresh" circle @click="handleRefresh"></el-button>
          </template>
        </el-table-column>

      </el-table>
      <!--    页码-->
      <div class="pageBox" style="bottom:0;position: absolute;width: 100%">
        <el-pagination
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="pageSize"
          layout="prev, pager, next"
          :total="total">
        </el-pagination>
      </div>
    </div>
<!--对话框-->
    <el-dialog
      title="详情"
      :visible.sync="dialogVisible"
      width="50%">
<!--表单模式-->
<!--      <el-form label-position="left" label-width="80px" :model="form">-->
<!--        <el-form-item label="名称">-->
<!--          <el-input :disabled="true" v-model="form.applicantName"></el-input>-->
<!--        </el-form-item>-->
<!--        <el-form-item label="身份证号">-->
<!--          <el-input :disabled="true" v-model="form.applicantPersonId"></el-input>-->
<!--        </el-form-item>-->
<!--        <el-form-item label="联系电话">-->
<!--          <el-input :disabled="true" v-model="form.applicantPhone"></el-input>-->
<!--        </el-form-item>-->
<!--        <el-form-item label="额外信息">-->
<!--          <el-input :disabled="true" v-model="form.additionalInfo"></el-input>-->
<!--        </el-form-item>-->
<!--      </el-form>-->
      <el-descriptions title="用户信息">
        <el-descriptions-item label="用户名">{{form.applicant_name}}</el-descriptions-item>
        <el-descriptions-item label="手机号">{{ form.applicant_phone }}</el-descriptions-item>
        <el-descriptions-item label="身份证号">{{ form.applicant_person_id }}</el-descriptions-item>
        <el-descriptions-item label="状态">
          <el-tag size="small">{{showState()}}</el-tag>
        </el-descriptions-item>
        <el-descriptions-item label="附加信息">
          {{ form.additional_information===null?'无':form.additional_information }}
        </el-descriptions-item>
      </el-descriptions>
<!--      label-position="left" label-width="80px"-->
      <el-form :model="form" :rules="rules" ref="form" :hide-required-asterisk="true">
        <el-form-item label="审批意见" prop="is_succeed">
          <el-select v-model="form.is_succeed"  placeholder="请选择审批意见">
            <el-option label="批准" value="1"></el-option>
            <el-option label="拒绝" value="0"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="handleCancel">取 消</el-button>
        <el-button type="primary" @click="handleSubmit">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import cookie from 'js-cookie';
import {getTester} from "@/api/getTester";
import { postTester } from "@/api/postTester";

export default { name: "PersonCertification",
  data(){
    return{
      pageSize:8,//页面大小
      currentPage:1,// 当前显示页码
      total:0,//总条数,
      rules:{
        is_succeed:[
          {
            required: true,
            message: '请选择审批意见',
            trigger: 'change',
          }
        ]
      },
      form:{//表单数据
        additional_information: "",
        applicant_name: "",
        applicant_person_id: "",
        applicant_phone: "",
        application_id:"",
        is_processed:0,
        is_succeed:0,
        result_info:"",
        apply_type:0,
        openid:""
      },
      dialogVisible:false,
      //接受的数据
      infoList:[],
      //本地数据
      testList:[
        {
          additionalInfo: "",
          applicantName: "fy",
          applicantPersonId: "456789012345678",
          applicantPhone: "13317278716",
          id:"15896953",
          isProcessed:0,
          isSucceed:0,
          resultInfo:"",
          type:0
        },
        {
          additionalInfo: "",
          applicantName: "fy",
          applicantPersonId: "2567895678",
          applicantPhone: "133278716",
          id:"158961953",
          isProcessed:0,
          isSucceed:0,
          resultInfo:"",
          type:0
        },
        {
          additionalInfo: "",
          applicantName: "fy",
          applicantPersonId: "123015678",
          applicantPhone: "133278716",
          id:"15893",
          isProcessed:0,
          isSucceed:0,
          resultInfo:"",
          type:0
        },

      ],
    }
  },
  methods:{
  handleCurrentChange(val){
    console.log(val)
    this.getInfo(this.currentPage)
  },
   getInfo(curPage){
     let param={token:cookie.get('token'), page:curPage, size:this.pageSize}
      getTester(param).then(res=>{
        console.log(res)
        this.infoList=res.data.data.application_list
        this.total=res.data.data.total
        this.$message({
          message: '加载成功',
          type: 'success'
        });
      }).catch(err=>{
        console.log(err)
        this.$message.error('网络异常!')
      })

    },
    handleRefresh(){
      this.currentPage=1
      this.getInfo(this.currentPage)
    },
    handleRow(row){
      if (this.$refs['form']!==undefined) {//防止验证信息残留
        this.$refs['form'].resetFields();
      }
       console.log(row)
      this.form=JSON.parse(JSON.stringify(row))
      this.form.is_succeed=null
      this.dialogVisible=true
    },
    showState(){
      if(this.form.is_processed===1){
        return "已查看"
      }else
        return "未完成"
    },
    handleCancel(){
      this.$refs.form.resetFields();
      this.dialogVisible = false;
    },
    handleSubmit(){
    this.$refs.form.validate((valid)=>{
      if(valid){
        this.dialogVisible = false;
        this.form.is_processed=1;
        this.form["token"]=cookie.get('token')
        console.log(this.form)
        postTester(this.form).then(res=>{
          console.log(res)
        })
        this.currentPage=1
        this.getInfo(this.currentPage)
      }
    })
    },
    handleChange(val){
      this.currentPage=val
      console.log(this.currentPage)
      this.getData(val)

    },
  },
  created() {
  //页面加载就获取数据
  //    let info=await getTester(cookie.get('token'))
  //    this.infoList=info.data.application_list
  //    this.total=this.infoList.length
    this.getInfo(this.currentPage)
     console.log('info',this.infoList)
    console.log('total',this.total)
  }


};
</script>

<style lang="less" scoped>
.manageBox{
  height: 98%;
  .box-card{
    box-shadow:0 2px 4px 0 rgba(0, 0, 0, 0.2);
    padding: 10px;
    height: 100%;
    background-color: white;
    border-radius:10px;
    position:relative
  }
  .pageBox{
    text-align: center;
  }
}
.el-input{
  width: 40%;
}

</style>