import axios from 'axios';
const BASE_URL = 'http://122.9.132.101:8080';

export async function postTesterInfo(data){
	let to_url = BASE_URL + "/web/admin/post_tester_apply";
	let getRes;
	await axios.post(to_url,data)
		.then(res=>{
			getRes=res.data
		}).catch(err=>{
			console.log(err);
		})
	return getRes
}

export async function getTesterInfo(option){
		let to_url = BASE_URL + option.url;
	  let getRes;
		await axios.get(to_url,{params: {token: option.token }})
			.then(res=>{
				getRes=res.data
			}).catch(err=>{
				console.log(err);
			})
	return getRes
}

// options={method:'method',url:'url'}
export function request(options) {
	// console.log( options)
	// get&post
	return options.method === 'get' ? get(options) : post(options);
	// get
	async function get(option) {
		// let to_url = transUrl(option);
		let to_url = BASE_URL + option.url;
		let getRes
		// console.log(to_url);
	  await axios.get(to_url,{params:option.data})
				.then(res => {
					getRes=res.data
				})
				.catch(err => {
					console.log(err);
				});
		return getRes
		// return axios
		// 	.get(to_url)
		// 	.then(res => {
		// 		console.log(res);
		// 	})
		// 	.catch(err => {
		// 		console.log(err);
		// 	});
	}
	//post
	function post(option) {
		let to_url = BASE_URL + options.url;
		return axios
			.post(
				to_url,
				{
					username: option.username,
					password: option.password,
				},
				{
					headers: { 'Access-Control-Allow-Origin': '*' },
				}
			)
			.then(res => {
				console.log(res);
			});
	}
}
