<template>
  <div class="mainBox">
    <div style="margin-bottom: 10px">
      <el-button type="primary" icon="el-icon-plus" @click="handleAdd">新增</el-button>
      <el-button type="primary" icon="el-icon-refresh"  @click="handleRefresh">刷新</el-button>
    </div>
    <div class="siteBox">
      <el-table
        :data="infoList"
        stripe
        max-height="600"
        style="width: 100%">

        <el-table-column
          prop="collection_point_id"
          label="序号"
          width="180">
        </el-table-column>
        <el-table-column
          label="地区"
          width="180">
          <template slot-scope="scope">
            {{(scope.row.collection_point_position||"").replace(/:/g,'')}}
          </template>
        </el-table-column>
        <el-table-column
          prop="collection_point_institution"
          label="负责机构"
          width="180">
        </el-table-column>
        <el-table-column
          prop="collection_point_principal"
          label="负责人"
          width="180">
        </el-table-column>
        <el-table-column
          prop="collection_point_contact_phone"
          label="联系电话">
        </el-table-column>
        <el-table-column
          label="操作"
          width="120"
          fixed="right">
          <template slot-scope="scope">
            <el-button
              @click.native.prevent="handleRow(scope.row)"
              type="text"
              size="small">
              编辑
            </el-button>
          </template>
        </el-table-column>


      </el-table>
      <div class="pageBox" style="bottom:0;position: absolute;width: 100%;text-align: center;">
        <el-pagination
          layout="prev, pager, next"
          :current-page.sync="currentPage"
          :page-size="pageSize"
          @current-change="handleChange"
          :total="total">
        </el-pagination>
      </div>
    </div>
    <el-dialog
      title="新增采集点"
      :visible.sync="dialogVisible"
      width="50%">
      <el-form label-position="left" label-width="80px" :model="form" :rules="rules" ref="form" >
        <el-form-item label="地区位置" prop="selectedOptions">
          <el-cascader
            size="large"
            :options="options"
            v-model="form.selectedOptions"
            @change="handleSelect">
          </el-cascader>
        </el-form-item>
        <el-form-item label="具体位置" prop="collection_point_location">
          <el-input v-model="form.collection_point_location"></el-input>
        </el-form-item>
        <el-form-item label="负责机构" prop="collection_point_institution">
          <el-input  v-model="form.collection_point_institution"></el-input>
        </el-form-item>
        <el-form-item label="负责人" prop="collection_point_principal">
          <el-input  v-model="form.collection_point_principal"></el-input>
        </el-form-item>
        <el-form-item label="联系电话" prop="collection_point_contact_phone">
          <el-input  v-model="form.collection_point_contact_phone"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm">提交</el-button>
          <el-button @click="resetForm">重置</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
<!--    编辑采集点-->
    <el-dialog
      title="编辑采集点"
      :visible.sync="dialogVisible1"
      width="50%">
      <el-form label-position="left" label-width="80px" :model="form1" :rules="rules" ref="form1" >
        <el-form-item label="地区位置" prop="selectedOptions">
          <el-cascader
            size="large"
            :options="options"
            v-model="form1.selectedOptions"
            @change="handleSelect">
          </el-cascader>
        </el-form-item>
        <el-form-item label="具体位置" prop="collection_point_location">
          <el-input v-model="form1.collection_point_location"></el-input>
        </el-form-item>
        <el-form-item label="负责机构" prop="collection_point_institution">
          <el-input  v-model="form1.collection_point_institution"></el-input>
        </el-form-item>
        <el-form-item label="负责人" prop="collection_point_principal">
          <el-input  v-model="form1.collection_point_principal"></el-input>
        </el-form-item>
        <el-form-item label="联系电话" prop="collection_point_contact_phone">
          <el-input  v-model="form1.collection_point_contact_phone"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm1">提交</el-button>
          <el-button @click="dialogVisible1 = false">取消</el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>

</template>

<script>
import cookie from "js-cookie";
import { getCollection } from "@/api/getCollection";
import { regionData,CodeToText,TextToCode } from 'element-china-area-data'
import { postCollection } from "@/api/postCollection";
import { postUpdateCollection } from "@/api/postUpdateCollection";

export default {
  name: "CollectionPoint",
  data(){
    return{
      options: regionData,
      selectedOptions: [],
      pageSize:8,//页面大小
      currentPage:1,// 当前显示页码
      total:0,//总条数,
      infoList:[],
      dialogVisible:false,
      dialogVisible1:false,
      form:{
        collection_point_position:'',
        collection_point_institution:'',
        collection_point_location:'',
        collection_point_principal:'',
        collection_point_contact_phone:'',
        selectedOptions:'',
      },
      form1:{//用于编辑
        collection_point_position:'',
        collection_point_institution:'',
        collection_point_location:'',
        collection_point_principal:'',
        collection_point_contact_phone:'',
        selectedOptions:'',
      },
      rules:{
        selectedOptions:[
          {
            required: true,
            message: '请选择地区信息',
            trigger: 'change',
          }
        ],
        collection_point_location: [
          { required: true, message: '请输入位置信息', trigger: 'blur' },
        ],
        collection_point_institution: [
          { required: true, message: '请输入负责机构', trigger: 'blur' },
        ],
        collection_point_principal: [
          { required: true, message: '请输入负责人姓名', trigger: 'blur' },
        ],
        collection_point_contact_phone: [
          { required: true, message: '请输入联系电话', trigger: 'blur' },
        ],
      }


    }
  },
  methods:{
    reset(){
      this.form.selectedOptions='';
      this.form.collection_point_location='';
      this.form.collection_point_institution='';
      this.form.collection_point_principal='';
      this.form.collection_point_contact_phone='';

    },
    getCodeToText(codeArray) {
      // if (null === codeStr && null === codeArray) {
      //   return null;
      // } else if (null === codeArray) {
      //   codeArray = codeStr.split(",");
      // }
      let area = "";
      switch (codeArray.length) {
        case 1:
          area += CodeToText[codeArray[0]];
          break;
        case 2:
          area += CodeToText[codeArray[0]] + ":" + CodeToText[codeArray[1]];
          break;
        case 3:
          area +=
            CodeToText[codeArray[0]] +
            ":" +
            CodeToText[codeArray[1]] +
            ":" +
            CodeToText[codeArray[2]];
          break;
        default:
          break;
      }
      return area;
    },
    handleRow(row){
      this.form1=JSON.parse(JSON.stringify(row))
      let tmp=(this.form1.collection_point_position||"").split(":")
      console.log(tmp)
      let province=tmp[0]
      let city=tmp[1]
      let area=tmp[2]
      this.form1.selectedOptions=[TextToCode[province].code,TextToCode[province][city].code,TextToCode[province][city][area].code]
      this.dialogVisible1=true
    },
    handleSelect(value){
      console.log(value)
    },
    submitForm1(){//update
      this.$refs.form1.validate(valid=>{
        this.form1["token"]=cookie.get('token')
        this.form1.collection_point_position=this.getCodeToText(this.form1.selectedOptions)
        if(valid){
          postUpdateCollection(this.form1).then(res=>{
            this.dialogVisible1 = false;
            console.log(res)
            this.currentPage=1
            this.getInfo(this.currentPage)
          })
        }
      })
    },
    submitForm(){//add
      this.$refs.form.validate(valid=>{
        this.form["token"]=cookie.get('token')
        this.form.collection_point_position=this.getCodeToText(this.form.selectedOptions)
        console.log(this.form)
        if(valid){
          this.dialogVisible = false;
          postCollection(this.form).then(res=>{
            console.log(res)
            this.currentPage=1
            this.getInfo(this.currentPage)
          })
        }
      })
    },
    resetForm(){
      this.reset()
      if (this.$refs['form']!==undefined) {//防止验证信息残留
        this.$refs['form'].resetFields();
      }
    },
    handleChange(val){
      console.log(val)
      this.getInfo(this.currentPage)
    },
    handleAdd(){
      this.reset()
      this.dialogVisible=!this.dialogVisible
    },
    getInfo(curPage){
      let param={token:cookie.get('token'), page:curPage, size:this.pageSize}
      getCollection(param).then(res=>{
        console.log(res)
        this.infoList=res.data.data.point_list
        this.total=res.data.data.total
        this.$message({
          message: '加载成功',
          type: 'success'
        });
      }).catch(err=>{
        console.log(err)
        this.$message.error('网络异常!')
      })
    },
    handleRefresh(){
      this.currentPage=1
      this.getInfo(this.currentPage)
    },
  },
  created() {
    //页面加载就获取数据
    this.getInfo(this.currentPage)
    console.log('info',this.infoList)
    console.log('total',this.total)
  }
};
</script>

<style lang="less" scoped>

.mainBox{
  height: 100%;
  .siteBox{
    padding: 7px;
    height: 90%;
    background-color: white;
    border-radius:5px;
    position:relative;
  }
}

</style>