<template>
  <el-tabs type="border-card">
    <el-tab-pane label="省级统计">
      <el-form label-position="left" :model="form" :rules="rules" ref="form" :inline="true">
        <el-form-item label="省级选择" prop="selectedOptions">
          <el-select v-model="form.selectedOptions" placeholder="请选择省份">
            <el-option
              v-for="item in provinceOptions"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="Submit">查询</el-button>
        </el-form-item>
      </el-form>
      <el-row v-if="p_flag">
        <el-col :span="10">
          <el-descriptions  direction="vertical" :column="3" border>
            <el-descriptions-item label="省份" :span="3">
              {{this.form.selectedOptions}}
            </el-descriptions-item>
            <el-descriptions-item label="总计" >
              <el-tag size="small">{{ provinceData.negative+provinceData.positive }}</el-tag>
            </el-descriptions-item>
            <el-descriptions-item label="阴性" >
              <el-tag size="small" type="success">{{ provinceData.negative }}</el-tag>
            </el-descriptions-item>
            <el-descriptions-item label="阳性">
              <el-tag size="small" type="danger">{{ provinceData.positive}}</el-tag>
            </el-descriptions-item>
          </el-descriptions>
        </el-col>
        <el-col :span="14">
          <e-charts class="chart" :option="POption"/>
        </el-col>
      </el-row>
    </el-tab-pane>

    <el-tab-pane label="市级统计">
      <el-form label-position="left" :model="cityForm" :rules="rules" ref="cityForm" :inline="true">
        <el-form-item label="市级选择" prop="selectedOptions">
          <el-cascader
            placeholder="请选择城市"
            size="large"
            :options="cityOptions"
            v-model="cityForm.selectedOptions">
          </el-cascader>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="citySubmit">查询</el-button>
        </el-form-item>
      </el-form>
      <el-row v-if="c_flag">
        <el-col :span="10">
          <el-descriptions  direction="vertical" :column="3" border>
            <el-descriptions-item label="城市" :span="3">
              {{(getCodeToText(this.cityForm.selectedOptions)||"").replace(/:/g,'')}}
            </el-descriptions-item>
            <el-descriptions-item label="总计" >
              <el-tag size="small">{{ cityData.negative+cityData.positive }}</el-tag>
            </el-descriptions-item>
            <el-descriptions-item label="阴性" >
              <el-tag size="small" type="success">{{ cityData.negative }}</el-tag>
            </el-descriptions-item>
            <el-descriptions-item label="阳性">
              <el-tag size="small" type="danger">{{ cityData.positive}}</el-tag>
            </el-descriptions-item>
          </el-descriptions>
        </el-col>
        <el-col :span="14">
          <e-charts class="chart" :option="COption"/>
        </el-col>
      </el-row>
    </el-tab-pane>

    <el-tab-pane label="区级统计">
      <el-form label-position="left" :model="districtForm" :rules="rules" ref="districtForm" :inline="true">
        <el-form-item label="地区选择" prop="selectedOptions">
          <el-cascader
            placeholder="请选择地区"
            size="large"
            :options="districtOptions"
            v-model="districtForm.selectedOptions">
          </el-cascader>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="districtSubmit">查询</el-button>
        </el-form-item>
      </el-form>
      <el-row v-if="d_flag">
        <el-col :span="10">
          <el-descriptions  direction="vertical" :column="3" border>
            <el-descriptions-item label="地区" :span="3">
              {{(getCodeToText(this.districtForm.selectedOptions)||"").replace(/:/g,'')}}
            </el-descriptions-item>
            <el-descriptions-item label="总计" >
              <el-tag size="small">{{ districtData.negative+districtData.positive }}</el-tag>
            </el-descriptions-item>
            <el-descriptions-item label="阴性" >
              <el-tag size="small" type="success">{{ districtData.negative }}</el-tag>
            </el-descriptions-item>
            <el-descriptions-item label="阳性">
              <el-tag size="small" type="danger">{{ districtData.positive}}</el-tag>
            </el-descriptions-item>
          </el-descriptions>
        </el-col>
        <el-col :span="14">
          <e-charts class="chart" :option="DOption"/>
        </el-col>
      </el-row>
    </el-tab-pane>
  </el-tabs>
</template>

<script>
import cookie from "js-cookie";
import { getTestInfo } from "@/api/getTestInfo";
import {regionData,CodeToText,provinceAndCityData,TextToCode } from 'element-china-area-data'
export default {
  name: "TestResult",
  data(){
    return{
      CName:'',
      DName:'',
      PName:'',
      p_flag:false,
      c_flag:false,
      d_flag:false,
      provinceOptions:[
        {
          value: '北京市',
          label: '北京市'
        },
        {
          value: '天津市',
          label: '天津市'
        },
        {
          value: '河北省',
          label: '河北省'
        },
        {
          value: '山西省',
          label: '山西省'
        },
        {
          value: '辽宁省',
          label: '辽宁省'
        },
        {
          value: '上海市',
          label: '上海市'
        },
        {
          value: '江苏省',
          label: '江苏省'
        },
        {
          value: '浙江省',
          label: '浙江省'
        },
        {
          value: '安徽省',
          label: '安徽省'
        },
        {
          value: '福建省',
          label: '福建省'
        },
        {
          value: '江西省',
          label: '江西省'
        },
        {
          value: '山东省',
          label: '山东省'
        },
        {
          value: '河南省',
          label: '河南省'
        },
        {
          value: '湖北省',
          label: '湖北省'
        },
        {
          value: '湖南省',
          label: '湖南省'
        },
        {
          value: '广东省',
          label: '广东省'
        },
        {
          value: '广西壮族自治区',
          label: '广西壮族自治区'
        },
        {
          value: '重庆市',
          label: '重庆市'
        },
        {
          value: '四川省',
          label: '四川省'
        },
        {
          value: '贵州省',
          label: '贵州省'
        },
        {
          value: '陕西省',
          label: '陕西省'
        },
        {
          value: '甘肃省',
          label: '甘肃省'
        },
        {
          value: '香港特别行政区',
          label: '香港特别行政区'
        },
        {
          value: '澳门特别行政区',
          label: '澳门特别行政区'
        },
      ],
      form:{
        selectedOptions:'',
        province:'',
        city:'',
        district:'',
      },
      cityForm:{
        selectedOptions:'',
        province:'',
        city:'',
        district:'',
      }, districtForm:{
        selectedOptions:'',
        province:'',
        city:'',
        district:'',
      },
      districtOptions: regionData,
      cityOptions: provinceAndCityData,
      provinceData:{
        positive:'',
        negative:'',
      },
      cityData:{
        positive:'',
        negative:'',
      },
      districtData:{
        positive:'',
        negative:'',
      },
      rules:{
        selectedOptions:[
          {
            required: true,
            message: '请选择位置信息',
            trigger: 'change'
          }
        ],
      },
    }
  },
  computed:{
    POption(){
      return{
        title: {
          text: '核酸检测结果统计',
          subtext:this.PName,
          left: 'center'
        },
        tooltip: {
          trigger: 'item'
        },
        legend: {
          orient: 'horizontal',
          left: 'center',
          top:'bottom',
        },
        series: [
          {
            name: '人数',
            type: 'pie',
            radius: '50%',
            data: [
              { value: this.provinceData.negative, name: '阴性',itemStyle: { color: '#66c03b' } },
              { value: this.provinceData.positive, name: '阳性',itemStyle: { color: '#f17173' } }
            ],
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          }
        ]
      }
    },
    COption(){
      return{
        title: {
          text: '核酸检测结果统计',
          subtext:this.CName,
          left: 'center'
        },
        tooltip: {
          trigger: 'item'
        },
        legend: {
          orient: 'horizontal',
          left: 'center',
          top:'bottom',
        },
        series: [
          {
            name: '人数',
            type: 'pie',
            radius: '50%',
            data: [
              { value: this.cityData.negative, name: '阴性',itemStyle: { color: '#66c03b' } },
              { value: this.cityData.positive, name: '阳性',itemStyle: { color: '#f17173' } }
            ],
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          }
        ]
      }
    },
    DOption(){
      return{
        title: {
          text: '核酸检测结果统计',
          subtext:this.DName,
          left: 'center'
        },
        tooltip: {
          trigger: 'item'
        },
        legend: {
          orient: 'horizontal',
          left: 'center',
          top:'bottom',
        },
        series: [
          {
            name: '人数',
            type: 'pie',
            radius: '50%',
            data: [
              { value: this.districtData.negative, name: '阴性',itemStyle: { color: '#66c03b' } },
              { value: this.districtData.positive, name: '阳性',itemStyle: { color: '#f17173' } }
            ],
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          }
        ]
      }
    },
  },
  methods:{
    getCodeToText(codeArray) {
      // if (null === codeStr && null === codeArray) {
      //   return null;
      // } else if (null === codeArray) {
      //   codeArray = codeStr.split(",");
      // }
      let area = "";
      switch (codeArray.length) {
        case 1:
          area += CodeToText[codeArray[0]];
          break;
        case 2:
          area += CodeToText[codeArray[0]] + ":" + CodeToText[codeArray[1]];
          break;
        case 3:
          area +=
            CodeToText[codeArray[0]] +
            ":" +
            CodeToText[codeArray[1]] +
            ":" +
            CodeToText[codeArray[2]];
          break;
        default:
          break;
      }
      return area;
    },
    Submit(){
      this.$refs.form.validate(valid=>{
        if(valid){
          this.PName=this.form.selectedOptions
          let param={token:cookie.get('token'), province:this.form.selectedOptions}
          getTestInfo(param).then(res=>{
            this.provinceData=res.data.data
            this.p_flag=true
          }).catch(err=>{
            console.log(err)
          })
        }
      })
    },
    citySubmit(){
      this.$refs.cityForm.validate(valid=>{
        if(valid){
          let tmp=this.getCodeToText(this.cityForm.selectedOptions)
          this.cityForm.province=tmp.split(':')[0]
          this.cityForm.city=tmp.split(':')[1]
          console.log( this.cityForm.province,this.cityForm.city)
          this.CName=this.cityForm.province+this.cityForm.city
          let param={token:cookie.get('token'), province:this.cityForm.province, city:this.cityForm.city}
          getTestInfo(param).then(res=>{
            console.log(res)
            this.cityData=res.data.data
            this.c_flag=true
          }).catch(err=>{
            console.log(err)
          })
        }
      })
    },
    districtSubmit(){
      this.$refs.districtForm.validate(valid=>{
        if(valid){
          let tmp=this.getCodeToText(this.districtForm.selectedOptions)
          this.districtForm.province=tmp.split(':')[0]
          this.districtForm.city=tmp.split(':')[1]
          this.districtForm.district=tmp.split(':')[2]
          this.DName=this.districtForm.province+this.districtForm.city+this.districtForm.district
          let param={token:cookie.get('token'), province:this.districtForm.province, city:this.districtForm.city, district:this.districtForm.district}
          getTestInfo(param).then(res=>{
            console.log(res)
            this.districtData=res.data.data
            this.d_flag=true
          }).catch(err=>{
            console.log(err)
          })
        }
      })
    },
  },

};
</script>

<style scoped>
.chart{
  height: 400px;
}
</style>