<template>
	<div class="loginbody">
      <div class="logindata">
        <div class="logintext">
          <h2>防疫管理系统</h2>
        </div>
        <div class="formdata">
          <el-form :model="form" :rules="rules" ref="loginform">
            <el-form-item prop="username">
              <el-input
                clearable
                @keyup.enter.native="handleUp"
                v-model="form.username"
                placeholder="请输入账号"
              >
                <template v-slot:prepend>
                  <el-button icon="el-icon-user"></el-button>
                </template>
              </el-input>
            </el-form-item>
            <el-form-item prop="password">
              <el-input
                ref="psw"
                clearable
                v-model="form.password"
                placeholder="请输入密码"
                show-password
                @keyup.enter.native="submit"
              >
                <template v-slot:prepend>
                  <el-button icon="el-icon-lock"></el-button>
                </template>
              </el-input>
            </el-form-item>
          </el-form>
        </div>
        <div class="butt">
          <el-button type="primary" @click="submit" style="width: 100px">登录</el-button>
        </div>
      </div>

<!--    测试按钮-->
<!--    <div class="fortest">-->
<!--      <el-button type="primary" @click="localtoken">本地token</el-button>-->
<!--      <el-button type="primary" @click="deletelocaltoken">删除本地token</el-button>-->
<!--    </div>-->
	</div>
</template>

<script>
	import { login } from '@/api/login';
	import cookie from 'js-cookie';
	export default {
		name: 'WebLogin',
		data() {
			return {
				form: {
					password: '',
					username: '',
				},
				rules: {
					username: [
						{
							required: true,
							message: '请输入用户名',
							trigger: 'blur',
						},
					],
					password: [
						{
							required: true,
							message: '请输入密码',
							trigger: 'blur',
						},
					],
				},
			};
		},
		methods: {
      handleUp(){
        this.$refs.psw.focus()
      },
			submit() {
				this.$refs.loginform.validate(async valid => {
					if (valid) {
						// let user_info=login(this.form);
            let user_info;
            await login(this.form).then(res=>{user_info=res})
             console.log(user_info);
            if(user_info===undefined){
              this.$message.error('网络异常!')
            }else if(user_info.statusCode===201){
              this.$message.error('用户不存在!')
            }else if(user_info.statusCode===202){
              this.$message.error('密码错误!')
            }else if(user_info.statusCode===0){
              cookie.set('token',user_info.data.token);
              this.$message({
                message: '登录成功',
                type: 'success'
              });
              await this.$router.push('/home')
              setTimeout(()=>{
                const h = this.$createElement;
                this.$notify({
                  title: '管理员',
                  message: h('i', { style: 'color: teal'}, '欢迎回来')
                });
              },300)
            }
					}
				});
				// cookie.set('token',token)
				// this.$router.push('/home')
			},
      localtoken(){
        let token=123
        cookie.set('token',token)
        this.$message({
          message: '登录成功',
          type: 'success'
        });
        setTimeout(()=>{
          this.$router.push('/home')
          setTimeout(()=>{
            const h = this.$createElement;
            this.$notify({
              title: '管理员',
              message: h('i', { style: 'color: teal'}, '欢迎回来')
            });
          },300)
        }, 1000)
      },
      deletelocaltoken(){
        cookie.remove('token')
      }
		},
	};
</script>

<style scoped>


	.loginbody {
    background-image: url("../assets/image/img_3.png");
    width: 100%;
		height: 100%;
		min-width: 1000px;
		/*background-image: url("");*/
		background-size: 100% 100%;
		background-position: center center;
		overflow: auto;
		background-repeat: no-repeat;
		position: fixed;
		line-height: 100%;
		padding-top: 150px;
	}

	.logintext {
		margin-bottom: 50px;
		line-height: 30px;
		text-align: center;
		font-size: 28px;
		font-weight: 100;
		color: #195c9d;
		/*text-shadow: 0 2px 2px #ffffff;*/
	}

	.logindata {
    border-radius: 10px;
    background-color: rgba(255, 255, 255, 0.6);
    padding: 50px;
		width: 400px;
		height: 300px;
		transform: translate(-50%);
		margin-left: 50%;
	}

	.butt {
		margin-top: 10px;
		text-align: center;
	}

</style>
