<template>
<div>
  <el-tabs type="border-card">
    <el-tab-pane label="省级统计">
      <el-form label-position="left" :model="provinceForm" :rules="rules" ref="provinceForm" :inline="true">
        <el-form-item label="省级选择" prop="province">
                    <el-select v-model="provinceForm.province" placeholder="请选择">
                      <el-option
                        v-for="item in provinceOptions"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                      </el-option>
                    </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="Submit3">查询</el-button>
        </el-form-item>
      </el-form>
      <el-row v-if="p_flag">
        <el-col :span="12">
          <el-table
            :data="provinceData"
            border
            style="width: 100%">
            <el-table-column
              prop="city"
              label="省份"
              width="100">
            </el-table-column>
            <el-table-column
              label="高风险地区"
              prop="high_level_number"
              width="160">
              <template slot-scope="scope">
                <el-tag size="small" type="danger">
                  {{scope.row.high_level_number}}
                </el-tag>
              </template>
            </el-table-column>
            <el-table-column
              prop="medium_level_number"
              label="中风险地区"
              width="160">
              <template slot-scope="scope">
                <el-tag size="small" type="warning">
                  {{scope.row.medium_level_number}}
                </el-tag>
              </template>
            </el-table-column>
            <el-table-column
              prop="low_level_number"
              label="低风险地区">
              <template slot-scope="scope">
                <el-tag size="small" type="success">
                  {{scope.row.low_level_number}}
                </el-tag>
              </template>
            </el-table-column>
          </el-table>
        </el-col>
        <el-col :span="12">
          <e-charts class="chart" :option="POption"/>
        </el-col>
      </el-row>

    </el-tab-pane>
    <el-tab-pane label="市级统计">
      <el-form label-position="left" :model="form1" :rules="rules" ref="form1" :inline="true">
        <el-form-item label="市级选择" prop="selectedOptions">
          <el-cascader
            placeholder="请选择市级信息"
            size="large"
            :options="options1"
            v-model="form1.selectedOptions">
          </el-cascader>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="Submit1">查询</el-button>
        </el-form-item>
      </el-form>
      <el-row v-if="flag1">
        <el-col :span="10">
          <el-descriptions  direction="vertical" :column="3" border>
            <el-descriptions-item label="地区信息" :span="3">
              {{(getCodeToText(this.form1.selectedOptions)||"").replace(/:/g,'')}}
            </el-descriptions-item>
            <el-descriptions-item label="高风险地区">
              <el-tag size="small" type="danger">{{ overallData.high_level_number }}</el-tag>
            </el-descriptions-item>
            <el-descriptions-item label="中风险地区" >
              <el-tag size="small" type="warning">{{ overallData.medium_level_number }}</el-tag>
            </el-descriptions-item>
            <el-descriptions-item label="低风险地区">
              <el-tag size="small" type="success">{{ overallData.low_level_number }}</el-tag>
            </el-descriptions-item>

          </el-descriptions>
        </el-col>
        <el-col :span="14">
          <e-charts class="chart" :option="COption"/>
        </el-col>
      </el-row>
    </el-tab-pane>

    <el-tab-pane label="区级查询">
      <el-form label-position="left" :model="form" :rules="rules" ref="form" :inline="true">
      <el-form-item label="地区选择" prop="selectedOptions">
        <el-cascader
          placeholder="请选择地区信息"
          size="large"
          :options="options"
          v-model="form.selectedOptions">
        </el-cascader>
      </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="Submit2">查询</el-button>
        </el-form-item>
      </el-form>
      <el-row v-if="flag">
        <el-col :span="10">
          <el-descriptions  direction="vertical" :column="2" border>
            <el-descriptions-item label="地区信息" :span="2">
              {{(getCodeToText(this.form.selectedOptions)||"").replace(/:/g,'')}}
            </el-descriptions-item>
            <el-descriptions-item label="风险等级">
              <el-tag size="small" :type="riskTag(specificData.risk_level)">{{ riskLevel(specificData.risk_level) }}</el-tag>
            </el-descriptions-item>
            <el-descriptions-item label="阳性人数" >
              <el-tag size="small" type="danger">{{ specificData.positive_number }}</el-tag>
            </el-descriptions-item>


          </el-descriptions>
        </el-col>
      </el-row>
    </el-tab-pane>
  </el-tabs>
</div>
</template>

<script>
import { regionData,CodeToText,provinceAndCityData,TextToCode } from 'element-china-area-data'
import { getAllSituation } from "@/api/getAllSituation";
import cookie from "js-cookie";
import { getAreaSituation } from "@/api/getAreaSituation";
import { getProvinceSituation } from "@/api/getProvinceSituation";
export default {
  name: "RiskRegion",
  data(){
    return{
      sum1:0,//high
      sum2:0,//medium
      sum3:0,//low
      CName:'',
      DName:'',
      PName:'',
      provinceData:[],
      specificData:{
        risk_level:"",
        positive_number:"",
      },
      overallData:{
        high_level_number:"",
        medium_level_number:"",
        low_level_number:"",
      },
      flag1:false,
      p_flag:false,
      flag:false,
      value1:123,
      provinceOptions:[
        {
          value: '北京市',
          label: '北京市'
        },
        {
          value: '天津市',
          label: '天津市'
        },
        {
          value: '河北省',
          label: '河北省'
        },
        {
          value: '山西省',
          label: '山西省'
        },
        {
          value: '辽宁省',
          label: '辽宁省'
        },
        {
          value: '上海市',
          label: '上海市'
        },
        {
          value: '江苏省',
          label: '江苏省'
        },
        {
          value: '浙江省',
          label: '浙江省'
        },
        {
          value: '安徽省',
          label: '安徽省'
        },
        {
          value: '福建省',
          label: '福建省'
        },
        {
          value: '江西省',
          label: '江西省'
        },
        {
          value: '山东省',
          label: '山东省'
        },
        {
          value: '河南省',
          label: '河南省'
        },
        {
          value: '湖北省',
          label: '湖北省'
        },
        {
          value: '湖南省',
          label: '湖南省'
        },
        {
          value: '广东省',
          label: '广东省'
        },
        {
          value: '广西壮族自治区',
          label: '广西壮族自治区'
        },
        {
          value: '重庆市',
          label: '重庆市'
        },
        {
          value: '四川省',
          label: '四川省'
        },
        {
          value: '贵州省',
          label: '贵州省'
        },
        {
          value: '陕西省',
          label: '陕西省'
        },
        {
          value: '甘肃省',
          label: '甘肃省'
        },
        {
          value: '香港特别行政区',
          label: '香港特别行政区'
        },
        {
          value: '澳门特别行政区',
          label: '澳门特别行政区'
        },
      ],
      options: regionData,
      options1: provinceAndCityData,
      provinceForm:{
        selectedOptions:'',
        province:'',
      },
      form1:{
        selectedOptions:'',
        province:'',
        city:'',
      },
      form:{
        selectedOptions:'',
      },
      rules:{
        selectedOptions:[
          {
            required: true,
            message: '请选择地区信息',
            trigger: 'change',
          }
        ],
        province:[
          {
            required: true,
            message: '请选择省份信息',
            trigger: 'change',
          }
        ],
    }
  }
  },
  computed:{
    POption(){
      return{
        title: {
          text: '风险地区统计',
          subtext:this.PName,
          left: 'center'
        },
        tooltip: {
          trigger: 'item'
        },
        legend: {
          orient: 'horizontal',
          left: 'center',
          top:'bottom',
        },
        series: [
          {
            name: '地区数量',
            type: 'pie',
            radius: '50%',
            data: [
              { value: this.sum3, name: '低风险地区',itemStyle: { color: '#66c03b' } },
              { value: this.sum2, name: '中风险地区',itemStyle: { color: '#e8a657' } },
              { value: this.sum1, name: '高风险地区',itemStyle: { color: '#f17173' } }
            ],
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          }
        ]
      }
    },
    COption(){
      return{
        title: {
          text: '风险地区统计',
          subtext:this.CName,
          left: 'center'
        },
        tooltip: {
          trigger: 'item'
        },
        legend: {
          orient: 'horizontal',
          left: 'center',
          top:'bottom',
        },
        series: [
          {
            name: '人数',
            type: 'pie',
            radius: '50%',
            data: [
              { value: this.overallData.low_level_number, name: '低风险地区',itemStyle: { color: '#66c03b' } },
              { value: this.overallData.medium_level_number, name: '中风险地区',itemStyle: { color: '#e8a657' } },
              { value: this.overallData.high_level_number, name: '高风险地区',itemStyle: { color: '#f17173' } }
            ],
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          }
        ]
      }
    },
  },
  methods:{
    riskTag(level){
      if(level<=0){
        return "info"
      }else if(level===1){
        return "success"
      }else if(level===2){
        return "warning"
      }else if(level==3){
        return "danger"
      }
    },
    riskLevel(level){
      if(level<=0){
        return "暂无数据"
      }else if(level===1){
        return "低"
      }else if(level===2){
        return "中"
      }else if(level==3){
        return "高"
      }
    },
    getCodeToText(codeArray) {
      // if (null === codeStr && null === codeArray) {
      //   return null;
      // } else if (null === codeArray) {
      //   codeArray = codeStr.split(",");
      // }
      let area = "";
      switch (codeArray.length) {
        case 1:
          area += CodeToText[codeArray[0]];
          break;
        case 2:
          area += CodeToText[codeArray[0]] + ":" + CodeToText[codeArray[1]];
          break;
        case 3:
          area +=
            CodeToText[codeArray[0]] +
            ":" +
            CodeToText[codeArray[1]] +
            ":" +
            CodeToText[codeArray[2]];
          break;
        default:
          break;
      }
      return area;
    },
    Submit1(){//city
      this.$refs.form1.validate(valid=>{
        if(valid){
          let tmp=this.getCodeToText(this.form1.selectedOptions)
          this.form1.province=tmp.split(':')[0]
          this.form1.city=tmp.split(':')[1]
          this.CName=this.form1.province+this.form1.city
          console.log(this.form1.province,this.form1.city)
          let param={token:cookie.get('token'), province:this.form1.province, city:this.form1.city}
          getAllSituation(param).then(res=>{
            this.overallData=res.data.data
            this.flag1=true
            console.log(res)
          })
        }
      })
    },
    Submit3(){//省级查询
      this.$refs.provinceForm.validate(valid=>{
        if(valid){
          this.PName=this.provinceForm.province
          let param={token:cookie.get('token'), province:this.provinceForm.province}
          getProvinceSituation(param).then(res=>{
            this.provinceData=res.data.data.cities
            let len=this.provinceData.length//使用临时变量，将长度缓存起来，避免重复获取数组长度
            this.sum1=0;this.sum2=0;this.sum3=0;
            for(let i=0;i<len;i++){
              this.sum1+=this.provinceData[i].high_level_number
              this.sum2+=this.provinceData[i].medium_level_number
              this.sum3+=this.provinceData[i].low_level_number
            }
            this.p_flag=true
            console.log(res)
          }).catch(err=>{
            console.log(err)
          })
        }
      })
    },
    Submit2(){//district
      this.$refs.form.validate(valid=>{
        if(valid){
          let tmp=this.getCodeToText(this.form.selectedOptions)
          this.form.province=tmp.split(':')[0]
          this.form.city=tmp.split(':')[1]
          this.form.district=tmp.split(':')[2]
          this.DName=this.form.province+this.form.city+this.form.district
          let param={token:cookie.get('token'), province:this.form.province, city:this.form.city,district:this.form.district}
          getAreaSituation(param).then(res=>{
            this.specificData=res.data.data
            this.flag=true
            console.log(res)
          })
        }
      })
    },
  }
};
</script>

<style lang="less" scoped>
.chart{
  height: 400px;
}
.my-label {
  background: #E1F3D8;
}

.my-content {
  background: #FDE2E2;
}
</style>